.form {
    
    &__form-group {
        display: flex;
        flex-direction: column;
        border:none;
        padding: 0;
        margin-top: 36px;
        width: 303px;
    }
    &__input {
        height: 54px;
        border-radius: 5px;
        margin-top: 11px;
        padding:  5px 15px;
        width: 100%;
        border:none;
        font-size: 14px;
        color: $black-light;
    }
}